<template>
    <div ref="businessTrends" style="width: 600px; height: 400px;"></div>
</template>

<script setup>
import { onMounted, ref,onUnmounted, nextTick} from "vue";
import * as echarts from 'echarts';
const businessTrends = ref(null);
let  businessTrendsElement = null;
// 
onMounted(async () => {
    await nextTick(); //
 const businessTrendsElement = echarts.init(businessTrends.value);
  const option = {
    tooltip: {
      trigger: 'axis',
    },
    grid: {
      left: '3%', // 
      right: '3%', // 
      bottom: '3%', // 
      top: '5%', // 
      containLabel: true, // 
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: ['1', '2', '3', '4', '5', '6', '7','8'], // 
      axisLine: {
        show: false, // 
      },
      axisTick: {
        show: false, // 
      },
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        formatter: '{value}',
      },
    },
    series: [
      {
        name: '',
        type: 'line',
        data: [620, 132, 701, 234, 890, 430, 120,300], // 
        smooth: true, // 
        lineStyle: {
          width: 2,
          color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
            { offset: 0, color: '#62c1fe' },
            { offset: 0.5, color: '#ad90f7' },
            { offset: 1, color: '#e875f2' },
          ]),
        },
        symbolSize: (value, params) => {
          // 
          const seriesLength = option.series[0].data.length;
          // 
          if (params.dataIndex === Math.floor(seriesLength / 2)) {
            return 8; // 
          }
          return 8; // 
        },
      },
    ],
  };
  businessTrendsElement.setOption(option);
}
)
onUnmounted(() => {
    if ( businessTrendsElement!= null && businessTrendsElement.dispose) {
        businessTrendsElement.dispose();
    }
  });
</script>

