<template>
    <div ref="projectSigningOverview" style="width: 600px; height: 400px;"></div>
  </template>
  
  <script setup>
  import { ref, onMounted, onUnmounted, nextTick } from 'vue';
  import * as echarts from 'echarts';
  
  // DOM
  const projectSigningOverview = ref(null);
  let projectSigningOverviewElement= null;
  
  // ECharts
  onMounted(async () => {
    await nextTick(); // DOM
    const projectSigningOverviewElement = echarts.init(projectSigningOverview.value);
  const option = {
    legend: {
      orient: 'vertical',
      right: 9,
      align: 'left',
      top: 'center',
      textStyle: {
        rich: {
          name: {
            color: '#929292',
          },
          value: {
            color: '#000',
            fontWeight: 'bold',
            fontSize: 16, // 
          },
        },
      },
      data: [
        {
          name: ' {value|80}{name| /5800}',
          textStyle: { rich: { value: {}, name: {} } },
        },
        {
          name: ' {value|18}{name| /5800}',
          textStyle: { rich: { value: {}, name: {} } },
        },
        {
          name: ' {value|24}{name| /5800}',
          textStyle: { rich: { value: {}, name: {} } },
        },
      ],
      icon: 'circle', // 
    },
    series: [
      {
        name: 'Access From',
        type: 'pie',
        radius: ['50%', '65%'],
        center: ['22%', '50%'],
        avoidLabelOverlap: false,
        label: {
          show: false,
          position: 'center',
        },
        emphasis: {
          label: {
            show: false,
          },
        },
        labelLine: {
          show: false,
        },
        data: [
          { value: 80, name: ' {value|80}{name| /5800}' }, // 
          { value: 18, name: ' {value|18}{name| /5800}' }, // 
          { value: 24, name: ' {value|24}{name| /5800}' }, // 
        ],
        itemStyle: {
          borderRadius: 0,
          borderColor: '#fff',
          borderWidth: 0,
        },
      },
    ],
    graphic: [
      {
        type: 'text',
        left: '65%', // 
        top: '19%', // legend
        style: {
          // 
          rich: {
            a: {
              // 80
              fontSize: 16, // 
              fontWeight: 'bold', // 
              color: '#000', // 
            },
            b: {
              // /5800
              fontSize: 14, // 
              color: '#929292',
            },
          },
          text: ' {a|80} {b|/5800}', // 
          textAlign: 'center',
          fill: '#000', // rich
        },
      },
      {
        type: 'text',
        left: '18%',
        top: '40%',
        style: {
          text: '72%',
          textAlign: 'center',
          fill: '#000',
          fontSize: 20,
        },
      },
      {
        type: 'text',
        left: '18%',
        top: '55%',
        z: 100,
        style: {
          text: '',
          textAlign: 'center',
          fill: '#000',
          fontSize: 14,
        },
      },
    ],
  };
  projectSigningOverviewElement.setOption(option);

  });
  
  // ECharts
  onUnmounted(() => {
    if (projectSigningOverviewElement!= null && projectSigningOverviewElement.dispose) {
        projectSigningOverviewElement.dispose();
    }
  });
  </script>
  
  <style scoped>
  /* CSS */

  </style>
  