<template>
    <div ref="salesRanking" style="width: 600px; height: 400px;"></div>
  </template>
  
  <script setup>
  import { ref, onMounted, onUnmounted, nextTick } from 'vue';
  import * as echarts from 'echarts';
  
  // DOM
  const salesRanking = ref(null);
  let salesRankingElement= null;
  
  // ECharts
  onMounted(async () => {
    await nextTick(); // DOM
    const salesRankingElement = echarts.init(salesRanking.value);
  const option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow',
      },
    },
    grid: {
      left: '0%', // 
      right: '5%', // 
      bottom: '3%', // 
      top: '5%', // 
      containLabel: true, // 
    },
    xAxis: {
      type: 'value',
      boundaryGap: [0, 0.01],
      axisLine: {
        show: false, // 
      },
      axisTick: {
        show: false, // 
      },
      splitLine: {
        show: false, // x
      },
    },
    yAxis: {
      type: 'category',
      data: ['', '', ''], // 
      axisLine: {
        show: false, // 
      },
      axisTick: {
        show: false, // 
      },
      splitLine: {
        show: false, // y
      },
      axisLabel: {
        rich: {
          iconStyle: {
            fontSize: 20, // 20
          },
          nameStyle: {
            // 
            fontSize: 12, // 12
          },
        },
        formatter: function (value, index) {
          // indexvalue
          const rankIcons = {
            2: '{iconStyle|}',
            1: '{iconStyle|}',
            0: '{iconStyle|}',
          };
          return `${rankIcons[index]} {nameStyle|${value}}`;
        },
      },
    },
    series: [
      {
        name: '',
        type: 'bar',
        data: [920, 1032, 1201], // 
        barWidth: '30%', // 
        itemStyle: {
          borderRadius: [0, 5, 5, 0], // 
          color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
            // 
            { offset: 0, color: '#bdd1f6' },
            { offset: 1, color: '#4282ff' },
          ]),
        },
        label: {
          show: true, // 
          position: 'right', // 
          formatter: '{c}', // {c}
        },
      },
    ],
  };
  salesRankingElement.setOption(option);

  });
  
  // ECharts
  onUnmounted(() => {
    if (salesRankingElement!= null && salesRankingElement.dispose) {
        salesRankingElement.dispose();
    }
  });
  </script>
  
  <style scoped>
  /* CSS */

  </style>
  